.wrapper {
  max-width: 1335px;
  box-sizing: border-box;
  padding: 110 120px;
  margin: auto;
}

.wrapper a {
  display: inline-block;
  margin: 5px;
}

.wrapper a:first-child {
  margin-left: 0;
}

.slider-wrapper {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

.slide {
  height: 100vh;
  background-size: cover !important;
}

.slide::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
    bottom: 0;
    left: 0;
}

.previousButton {
		position: absolute;
		top: 50%;
		/* transform: translateY(-100%); */
		z-index: 1;
		/* background:url(./assets/vippng.com-left-and-right-arrow-3739353.png)  no-repeat center center / 99px; */
    width: 32px;
		height: 132px;
		/* text-indent: -9999px; */
		cursor: pointer;
}

 .nextButton {
		position: absolute;
		top: 50%;
		/* transform: translateY(-100%); */
		z-index: 1;
		/* background: url(./assets/vippng.com-arrow-pointing-right-png-36352.png) no-repeat center center / 100px; */
		width: 32px;
		height: 132px;
		/* text-indent: -9999px; */
		cursor: pointer;
}

