* {
  outline: none;
}




.wrapper {
  max-width: 1335px;
  box-sizing: border-box;
  padding: 110 120px;
  margin: auto;
}

.wrapper a {
  display: inline-block;
  margin: 5px;
}

.wrapper a:first-child {
  margin-left: 0;
}

.slider-wrapper {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

.slide {
  height: 100vh;
  background-size: cover !important;
}

.slide::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0, 0, 0, 0.9)));
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
    bottom: 0;
    left: 0;
}

.previousButton {
		position: absolute;
		top: 50%;
		/* transform: translateY(-100%); */
		z-index: 1;
		/* background:url(./assets/vippng.com-left-and-right-arrow-3739353.png)  no-repeat center center / 99px; */
    width: 32px;
		height: 132px;
		/* text-indent: -9999px; */
		cursor: pointer;
}

 .nextButton {
		position: absolute;
		top: 50%;
		/* transform: translateY(-100%); */
		z-index: 1;
		/* background: url(./assets/vippng.com-arrow-pointing-right-png-36352.png) no-repeat center center / 100px; */
		width: 32px;
		height: 132px;
		/* text-indent: -9999px; */
		cursor: pointer;
}

/* .previousButton:hover, .nextButton:hover {
  background: url(./assets/vippng.com-left-and-right-arrow-3739353.png)  no-repeat center center / 99px;
} */

.previousButton {
  left: 0;
  /* -webkit-transform: rotate(180deg) translateY(calc(50% + 0px));
          transform: rotate(180deg) translateY(calc(50% + 0px)); */
}

/* .previousButton:hover {
  left: -5px;
} */

.nextButton {
  right: 0;
}

.nextButton:hover {
  right: -1px;
}

.slider-content {
  text-align: center;
  margin: 110 70px;
}

.slider-content .inner {
  padding: 110 70px;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.slider-content .inner button{
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}

.slider-content .inner h1 {
  font-weight: 900;
  margin: 0 auto;
  max-width: 840px;
  color: #FFFFFF;
  font-size: 64px;
  line-height: 1;
}

.slider-content .inner p {
  color: #FFFFFF;
  font-size: 18px;
  line-height: 1.5;
  margin: 20px auto 30px;
  max-width: 640px;
}

.slider-content section {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.slider-content section span {
  color: #FFFFFF;
}

.slider-content section span {
  color: rgba(255, 255, 255, 0.5);
  font-size: 12px;
  display: inline-block;
  text-align: left;
  line-height: 1.4;
  vertical-align: middle;
  margin-left: 10px;
}

.slider-content section img {
  width: 40px;
  height: 100vh;
  border: solid 2px rgba(255, 255, 255, 0.5);
  border-radius: 100%;
  vertical-align: middle;
  padding: 0 70px;
}

.slider-content section span strong {
  color: #FFFFFF;
  /* font-size: 18px; */
  display: block;
}

@media (max-height: 500px) {
  .slider-wrapper, .slide {
    height: calc(100vh - 75px);
  }    
}

@media (max-width: 640px) {
  .slider-wrapper, .slide {
    height: calc(80vh - 75px);
  }    
}

@media (max-height: 600px) {
  .slider-content .inner h1 {
    font-size: 32px;
  }    
}

@media (max-width: 640px) {
  .slider-content .inner h1 {
    font-size: 32px;
  }
}